<template>
	<el-main class="main">
		<el-row :gutter="20">
			<el-col :span="2">
				<div class="grid-content ">电影表格</div>
			</el-col>
			<el-col :span="16">
				<el-col :span="10" class="text-right">
					<el-input v-model="search.keyword" prefix-icon="el-icon-search" :autofocus="true" placeholder="请输入关键字" size="small"></el-input>
					<el-button type="primary" icon="el-icon-search" size="small" style="margin-left: 10px;" round @click="searchData">查 询</el-button>
					<el-button icon="el-icon-refresh-left" size="small" style="margin-left: 10px;" round @click="resetData">重 置</el-button>
				</el-col>
			</el-col>
			<el-col :span="6">
				<div class="grid-content text-right">
					<el-button type="primary" icon="el-icon-plus" size="small" @click="createOne">新 建</el-button>
					<el-tooltip class="item" effect="dark" content="刷新" placement="top">
						<el-button type="text" style="font-size: 20px;height: 33px;box-sizing: border-box;padding: 0; width: 40px;"  @click="refreshData">
							<i class="el-icon-refresh"></i>
						</el-button>
					</el-tooltip>
				</div>
			</el-col>
		</el-row>
		<el-container style="margin: 10px auto 12px;">
			<el-table :data="tableData.date" :stripe="true" v-loading="loading">
				<el-table-column type="index" width="50"></el-table-column>
				<el-table-column prop="name" label="电影名称" width="260"></el-table-column>
				<el-table-column label="封面" width="200">
					<template slot-scope="scope"><div class="covers-box"><img :preview="scope.$index" class="covers" :src="scope.row.covers" alt="covers"></div></template>
				</el-table-column>
				<el-table-column label="上映时间" width="160">
					<template slot-scope="scope">{{ scope.row.release | dateFormat }}</template>
				</el-table-column>
				<el-table-column label="电影类型">
					<template slot-scope="scope">{{ scope.row.types | objectFormat }}</template>
				</el-table-column>
				<el-table-column label="导演">
					<template slot-scope="scope">{{ scope.row.director | stringArrayFormat }}</template>
				</el-table-column>
				<el-table-column prop="score" label="豆瓣平分" width="150"></el-table-column>
				<el-table-column label="制片国家">
					<template slot-scope="scope">{{ scope.row.countries | objectFormat }}</template>
				</el-table-column>
				
				<el-table-column label="操作" width="142">
					<template slot-scope="scope">
						<el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
						<el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-container>
		
		<el-pagination background :page-size="limit" :current-page="search.page" layout="prev, pager, next" :total="tableData.total" @current-change="currentChange"></el-pagination>
		
	</el-main>
</template>

<script>
	import type from '../services/type.js';
	import country from '../services/country.js';
	import movie from '../services/movie.js';
	import dayJS from 'dayjs';

	export default {
		name: 'MoviePage',
		filters: {
			dateFormat(e) {
				return dayJS(e).format('YYYY-MM-DD')
			},
			objectFormat(e){
				let str = ''
				for(let i in e){
					str += `${e[i].name} / `;
				}
				return str.substr(0, str.length - 2);
			},
			stringArrayFormat(e){
				let str = ''
				for(let i in e){
					str += `${e[i]} / `;
				}
				return str.substr(0, str.length - 2);
			},
		},
		watch: {
			'search.keyword'(value, old) {
				if (value.length <= 0) {
					this.refreshData();
				}
			},
			deep: true
		},
		data() {
			return {
				tableData: Array(),
				form: { //修改 创建
					name: '',
				},
				search: { //获取 查询
					keyword: '',
					page: 1
				},
				row: Object(),
				loading: false,
				limit: 13
			}
		},
		mounted() {
			this.getList();
		},
		methods: {
			currentChange(e){
				this.search.page = e;
				this.refreshData();
			},
			handleEdit(index, row) {
				const id = row._id;
				this.$router.push('/edit_movie/' + id);
				
			},
			async resetData() {
				if (this.search.keyword !== '') {
					this.search.keyword = '';
					await this.refreshData();
				}
			},
			searchData() {
				if (this.search.keyword !== ''){
					this.search.page = 1;
					this.refreshData();
				}
			},
			async refreshData() {
				this.loading = true;
				await this.getList();
				this.loading = false;
			},
			async handleDelete(index, row) {
				try {
					await this.$confirm(`此操作将永久删除“${row.name}	”该项, 是否继续?`, '提示', {
						type: 'warning'
					});
					this.loading = true;
					try {
						await country.remove({
							id: row._id
						});
						this.$message.success('删除成功');
						this.refreshData();
					} catch (e) {
						this.$message.error(e.error || '删除失败');
					}
					this.loading = false;
				} catch (e) {}
			},
			async getList() {
				this.search.limit = this.limit;
				this.tableData = await movie.get(this.search);
			},
			createOne() {
				this.$router.push('/creat_movie');
			},
		}
	}
</script>

<style lang="scss" scoped>
	.grid-content {
		line-height: 33px;
		font-size: 16px;
		color: rgba(0, 0, 0, .73);
		text-align: left;
	}

	.text-right {
		text-align: right;
		display: flex;
		align-items: flex-end;
		justify-content: flex-end;
	}
	.covers-box{
		height: 30px;
		width: 150px;
		overflow: auto;
		.covers{
			width: 100px;
			height: 100%;
			object-fit: cover;
		}
	}
</style>
